    <div id="das-content" data-wow-offset="50" data-wow-duration="1s" class="hello wow slideInUp">
      <div class="us-content-text">
        <h2 class="puniu-title">应用原理</h2>
        <div class="TheDivider"></div>
        <h3 class="secondaryTitle">浅层地震法</h3>
        <p class="puniu-the-document">
          浅层地震法是隐伏岩溶探测的重要方法。传统的浅层地震法需要利用专门的激励源产生振动，专门设置多个地震波检波器拾取地震波信息。每次实验只能检测一个位置的地质信息，只能在铁路维护时进行检测，费时费力，实用性不好。
          <br />
          <br />本方案提出一种基于光纤的浅层地震监测技术，采用高铁正常运行时的振动作为激励源，以铁路沿线铺设的光纤作为地震波检波器，高铁运行时轨道的振动作为激励源，产生的地震波被路基及地下结构反射后，到达铺设的光纤，该振动信息被设置在基站的解调仪系统捕获并记录下来。通过分析地震波的时序、频谱特征，可以获取隐伏岩溶的状态信息。该方案不干扰铁路的正常运行，能够对铁路沿线所有位置的路基进行实时监控，不仅能够发现溶洞等地质结构，而且能够及时发现路基下岩层的断裂、开缝等异常状况。
          <br />
          <br />该浅层地震勘探系统需要同时监控数公里至数十公里范围内大量位置处的振动信息。基于光纤的分布式声波（振动）传感器是利用光纤中的瑞利背向散射效应，以光纤同时作为光信号传导媒介和振动敏感元件的传感器。光纤在制造过程中，由于掺杂不均和制作工艺限制，光纤内部介质折射率并不均匀。当光遇到上述折射率变化时，会有一部分光偏离原来传播路径而形成背向散射。基于对背向散射光的探测，可以实现对外界参量进行感知。激光器发出的探测脉冲进入传感光纤，在传感光纤中传输的同时会不停的产生瑞利背向散射光。根据从探测光脉冲产生到散射光信号到达探测器的时间，可以区别传感光纤上不同位置产生的散射光；由光纤上的各点散射光的强度和相位变化情况，分析可得传感光纤的损耗、振动等信息。
          <br />
          <br />DAS传感技术所采用的光纤体积小、柔软可弯曲，能以任意形式复合于基体结构中而不影响基体的性能。只需从光纤的一端进行监测，就可以得到整根光纤上各处的振动信息，该传感技术系统最具优势的地方还在于光纤既是传感元件又是传输介质，属于分布式监测，可以满足长距离、不间断监测的要求，便于与光纤传输系统联网，以实现系统的遥测和控制。
        </p>
      </div>
    </div>
    <div id="advantage" data-wow-offset="50" data-wow-duration="1s" class="hello wow slideInUp">
      <div class="advantage-content">
        <h2 class="puniu-title">应用方法</h2>
        <div class="TheDivider"></div>
        <h3 class="secondaryTitle">解调系统的开发</h3>
        <p
          class="puniu-the-document"
        >DAS系统解调仪的性能指标非常重要。本项目采用的HiFi-DAS系统，是由上海交通大学区域光纤通信网与新型光通信系统国家重点实验室的光纤传感研究团队，在其提出的TGD-OFDR型光反射仪技术基础上，利用创新技术攻克空间分辨率、衰落噪声等问题，所实现的一种高性能DAS系统。不仅测量距离长，测量灵敏度高，而且整个传感光纤内具有良好的一致性，保障了地震检波的精度。</p>
      </div>
    </div>
    <div class="puniu-das-two hello wow slideInUp" data-wow-offset="0" data-wow-duration="3s">
      <h2>主要技术指标</h2>
      <ul class="puniu-das-two-information">
        <li class="puniu-das-two-list">
          <div class="pdtl-top">
            <h3>40</h3>
            <h4>km</h4>
          </div>
          <div class="pdtl-bottom">测量距离</div>
        </li>
        <li class="puniu-das-two-line"></li>
        <li class="puniu-das-two-list">
          <div class="pdtl-top">
            <h3>1-200</h3>
            <h4>hz</h4>
          </div>
          <div class="pdtl-bottom">频响范围</div>
        </li>
