    <div id="advantage" data-wow-offset="50" data-wow-duration="1s" class="hello wow slideInUp">
      <div class="advantage-content">
        <h3 class="secondaryTitle">传感光纤的布设</h3>
        <p class="puniu-the-document">
          根据检测原理，高铁运行时对铁轨产生的振动冲击，地震波传播到路基和地下结构，并被部分反射回来，传播到光纤位置处，引起光纤的振动，DAS解调仪检测光纤的振动信息，据此判定路基的状态。该检测方案中传感光纤作为地震检波器，需要与地面具有良好的耦合
          <br />
          <br />铁轨产生的冲击振动有两种途径到达传感光纤：地面直达波和地下反射波。地下反射波包含了地质信息，为了确保光纤能正确接收到地下反射回的地震波，传感光纤需要与地质层紧密耦合。而直达波的幅度过高会影响对地下反射波的检测，需要适当屏蔽直达波的幅度
        </p>
      </div>
    </div>
    <div id="advantage" data-wow-offset="50" data-wow-duration="1s" class="hello wow slideInUp">
      <div class="advantage-content">
        <h3 class="secondaryTitle">数据处理与信号分析</h3>
        <p class="puniu-the-document">
          岩溶洞主要分为空洞、充水或部分充水、部分充填沉积物三大类。无论哪一类, 其充填物的密度、波速都远比周围岩石小很多。由于密度差异，当入射波遇到充满空气的洞顶界面时, 能量将被反射回地面, 在地面上相应的观测点会接收到发生了180°相位变化的反射波。对于规模较小且充水的溶洞，反射系数将减小, 洞顶反射波能量也将相应减弱, 此时不能单纯从反射理论来识别洞顶反射信息，需要从绕射理论和“ 共鸣” 现象来识别有关反映“ 溶洞”存在的波形特征。根据绕射理论, 可把洞顶看成一个绕射点, 当入射波遇到这样的绕射点时, 记录上可能会出现绕射同相轴；所谓“共鸣”现象则把洞看成一个“次生震源”，入射波激发“次生震源”产生“共鸣”，从而在记录上出现波的“续至振荡”现象, 使连续的同相轴产生间断或杂乱。
          <br />
          <br />对DAS系统获取的地震波信号进行分析，根据已有的溶洞、空洞等位置的声波特征，利用溶洞对地震波的反射、绕射和“共鸣”现象，能够实现对溶洞存在与否的识别。
          <br />
          <br />此外，DAS系统可以一年四季不间断地对记录高铁经过时激起的反射波，通过比较铁路沿线的声波谱特征的变化情况，得到地质变化的信息，及时发现新形成的溶洞、冲刷出来的空腔、岩层的断裂等信息
        </p>
      </div>
    </div>
    <div id="das-content" data-wow-offset="50" data-wow-duration="1s" class="hello wow slideInUp">
